.LeftLight {
    position: absolute;
    left: -5%;
    top: 17%;
    width: 65vw; 
    z-index: -1;
    filter: drop-shadow(0 0 5rem #ecad00);
    img {
        
        width: inherit;
        animation-name: leftlight_anime;
        animation-duration: 2.8s;
        animation-iteration-count: infinite;
        // animation-fill-mode: forwards;
        animation-direction: alternate;
        // animation-iteration-count: 2;
        animation-timing-function: linear;
    }
}
.RightLight {
    right: -5%;
    position: absolute;
    top: 17%;
    width: 68vw;
    z-index: -1;
    filter: drop-shadow(0 0 5rem #ecad00);
    img {
        
        width: inherit;
        animation-name: rightlight_anime;
        animation-duration: 3.5s;
        animation-iteration-count: infinite;
        // animation-fill-mode: forwards;
        animation-direction: alternate;
        // animation-iteration-count: 2;
        animation-timing-function: linear;
    }
}
